.error textarea,
.error input {
    border-color: red;
}

.errorData {
    color: red;
}

.but {
    width: 170px;
    height: 30px;
    margin-top: 10px;
    cursor: pointer;
    color: #385899;
    font-weight: bold;
}

.input {
    border-color: #b4b4b4;
    border-width: 3px;
    border-radius: 4px;
    font-size: 16px;
    width: 270px;
    height: 23px;
}

.textarea {
    border-width: 3px;
    border-color: #aaaaaa;
    border-radius: 4px;
    box-shadow: inset 0 1px 4px #00cab7;
    font-size: 16px;
    width: 270px;
    height: 40px;
}


@media only screen and (min-width: 992px) {
    .input {
        height: 31px;
        width: 300px;
    }
    .textarea {
        height: 45px;
        width: 300px;
    }

}