.bottom {
    grid-area: b;
    background-color: black;
    height: 260px;
    color: white;
    display: flex;
    flex-direction: column;
    padding: 5px;
    justify-content: space-around;
}

.links{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.contacts{
    text-decoration: none;
    color: white;
    font-weight: bolder;
    padding-right: 20px;
    font-size: larger;
}
.contacts:hover{
    color: lightgrey;
}

.flex {
    display: flex;
    flex-direction: row;
    padding: 20px;
    justify-content: space-around;
}


.info{
    text-align: center;
}

@media only screen and (min-width: 992px) {
    .bottom{
        height: 230px;
    }
    .contacts{
        padding-right: 30px;
        font-size: 23px
    }
}