.content {
    padding: 0 15px;
    grid-area: c;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    height: max-content;
}

.imgContainer {
    height: 80%;
}

.img {
    max-height: 100%;
    margin: auto;
    max-width: 100%;
}

.item {
    text-align: center;
    margin-bottom: 70px;
    height: 200px;
}

.text {
    text-decoration: none;
    color: black;
    font-weight: bold;
    font-size: small;
}

.model {
    max-width: 150px;
}

.but {
    padding: 5px;
}

.but button {
    background-color: black;
    border-radius: 7px;
    height: 30px;
    width: 120px;
    border: none;
    cursor: pointer;
    color: white;
}


@media only screen and (min-width: 768px) {
    .but {
        display: none;
    }

    .item:hover .but {
        display: block;
    }

    .item {
        padding: 0 20px;
        height: 250px;
    }

    .but button:hover {
        background-color: #1e1e1e;
    }

    .but button {
        width: 140px;
        height: 35px;
    }

    .text {
        font-size: unset;
    }

    .model {
        max-width: 300px;
    }
}

@media only screen and (min-width: 992px) {
    .item {
        height: 300px;
    }
}
