.orders {
    grid-area: c;
    margin-top: 120px;
}

.buttons {
    height: 40px;
}

.button {
    height: 38px;
    width: 33.3%;
    font-size: 14px;
    border: none;
    color: #005e53;
    background-color: white;
}

.button:hover {
    background: #eaeaea;
}

.container {
    padding: 0 10px 10px 10px;
    display: grid;
    grid-template-columns: 8% 12% 30% 20% 15% 12%;
    border-bottom-style: dashed;
    border-bottom-width: 1px;
    border-color: #00ab9a;
    align-items: center;
    justify-content: center;
}

.fastContainer {
    padding: 0 10px 10px 10px;
    display: grid;
    grid-template-columns: 10% 15% 20% 20% 20% 11%;
    border-bottom-style: dashed;
    border-bottom-width: 1px;
    border-color: #00cab7;
    justify-content: center;
    align-items: center;
}

.title {
    font-size: 12px;
    color: #525252;
    text-align: center;
}

.row {
    margin: 0 auto;
    color: #767676;
    text-align: center;
    font-size: 9px;
}

.statusButton {
    border-radius: 10px;
    height: 22px;
    width: 55px;
    cursor: pointer;
    font-size: 8px;
    color: black;
    border-color: black;
    border-width: 1px;
    background-color: white;
}

.fastrow {
    padding-left: 10px;
    font-size: 10px;
}

.fastrow div {
    color: #6e6e6e;
}

.fastrow div span {
    color: #6e6e6e;
    font-weight: bold;
}

.empty {
    text-align: center;
    margin-top: 15%;
    font-size: large;
    font-weight: bold;
    color: #005b52;
    margin-bottom: 15%;
}


@media only screen and (min-width: 768px) {
    .orders {
        margin: 0;
    }

    .button {
        cursor: pointer;
        font-size: 18px;
    }

    .fastrow {
        font-size: 15px;
    }

    .row {
        font-size: 15px;
    }

    .statusButton {
        width: 80px;
        height: 28px;
        font-size: 12px;
    }

    .title {
        font-size: 20px;
    }

    .container {
        padding: 10px 0;
    }

    .fastContainer {
        padding: 10px 0;
    }

    .empty {
        font-size: 25px;
    }
}

@media only screen and (min-width: 992px) {
    .orders {
        padding: 0 15px;
    }

    .button {
        height: 40px;
        font-size: 20px;
    }

    .container {
        grid-template-columns: 10% 10% 20% 20% 18% 20%;
    }

    .fastrow {
        font-size: 18px;
    }

    .row {
        font-size: 18px;
    }

    .statusButton {
        width: 100px;
        height: 35px;
        font-size: 15px;
    }
}