.nav {
    height: 40px;
    padding-top: 10px;
    width: min-content;
    background-color: white;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    position: sticky;
    top:0;
}

.navItem {
    text-decoration: none;
    color: #235947;
    font-weight: bolder;
    display: block;
    padding: 10px 25px;
    margin: 0 5px;
    width: min-content;
    background-color: white;
}

.navItem:hover {
    border-bottom: 4px solid black;
}

.active {
    border-bottom: 4px solid black;
}