.form {
    padding-top: 20px;
    padding-bottom: 20px;
    margin: 0 30px;
    width: max-content;
    text-align: center;
}

.title {
    display: inline-block;
    font-weight: bolder;
    font-size: 25px;
    width: max-content;
    padding: 10px 0 15px 5px;
}

.butCont {
    padding-top: 20px;
    width: 200px;
    margin: 0 auto;
}

.but {
    background-color: white;
    border-radius: 10px;
    height: 40px;
    width: 200px;
    cursor: pointer;
    color: black;
    border-color: black;
}

.input {
    border-color: #b4b4b4;
    border-width: 1px;
    border-radius: 4px;
    font-size: 16px;
    width: 200px;
    height: 23px;
    margin: 10px 0;
    padding: 5px;
    -moz-appearance: textfield;
}

.radio {
    border: 10px solid #90DDD0;
    color: red;
    width: 25px;
    margin: 7px 5px;
    cursor: pointer;
}

.text {
    font-weight: bold;
}

.text div {
    width: 30px;
    display: inline-block;
}

@media only screen and (min-width: 768px) {
    .title {
        padding: 50px 0 15px 5px;
    }
    .form{
        padding-top: 50px;

        text-align: left;
    }
}