.cart {
    grid-area: c;
    margin-top: 120px;
    padding: 0 12px;
}

.empty {
    font-size: 22px;
    text-align: center;
    margin-top: 24%;
    margin-bottom: 24%;
}

.back {
    text-decoration: none;
    color: #235947;
}

.order {
    display: grid;
    padding: 0 10px;
    grid-template-columns: 35% 60% 5%;
    margin: 15px 0;
    max-width: 600px;
}

.imgblock {
    max-height: 120px;
    margin: auto;
    height: 110px;
}

.imgblock img {
    max-height: 100%;
    margin: auto;
    max-width: 100%;
}

.close img {
    width: 20px;
    height: 20px;
    cursor: pointer;
}

.setQualityForm {
    width: 60px;
}

.text {
    padding: 3px 0;
    color: #5f5f5f;
    font-size: small;
}

.text span {

    font-weight: bold;
}

.quantity {
    display: flex;
    flex-direction: row;
    height: 35px;
}

.quantity span {
    margin: auto 0;
    padding-right: 5px;
}

h4 {
    margin: 3px 0;
    color: #4b4b4b;
}

h3 {
    color: #4b4b4b;
}

.marginBottom {
    margin-bottom: 20px;
}

.form {
    margin: 0 auto;
    height: min-content;
    width: min-content;
    border: 1px white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    padding: 10px 23px;
}

.formItem {
    padding: 12px;
    font-size: 20px;
    color: #4b4b4b;
}

.butCont {
    padding-top: 20px;
    width: 200px;
    margin: 0 auto;
}

.butCont button {
    background-color: white;
    border-radius: 10px;
    height: 40px;
    width: 200px;
    cursor: pointer;
    color: black;
    border-color: black;
}

.radio {
    display: flex;
    flex-direction: column;
    padding: 7px;
}

@media only screen and (min-width: 642px) {
    .cart {
        padding: 0 25px;
    }

    .back {
        font-size: large;
    }

    .empty {
        margin-top: 10%;
        margin-bottom: 16%;
    }

    .imgblock {
        height: 140px;
        max-height: 150px;
    }

    .form {
        width: 570px;
        padding: 10px 12px;
    }

    .formItems {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .formItem {
        width: 260px;
        height: 60px;
        padding: 12px;
    }

    h4 {
        font-size: larger;
        margin: 8px 0;
    }

    .text {
        font-size: 15px;
    }

    .order {
        grid-template-columns: 40% 55% 5%;
    }

    .butCont {
        padding-top: 35px;
    }

}

@media only screen and (min-width: 768px) {
    .cart {
        margin: 0;
    }
}


@media only screen and (min-width: 992px) {
    .form {
        width: 680px;
        padding: 10px 15px;
    }

    .formItem {
        width: 300px;
        height: 60px;
        padding: 12px 20px;
    }

    .imgblock {
        min-height: 190px;
    }


    h4 {
        font-size: 21px;
    }

    .text {
        font-size: 18px;
    }

    .info {
        height: min-content;
        margin: auto 0;
    }

    .marginBottom {
        margin: 30px 0 40px 100px;
    }

    .order {
        max-width: 700px;
    }
}









