.page {
    margin-top: 130px;
}

.mobButton{
    margin-left: 25px;
    width: 150px;
    padding-bottom: 10px;
}
.mobButton button{
    background-color: white;
    border-radius: 5px;
    height: 38px;
    width: 150px;
    font-size: 16px;
    cursor: pointer;
    color: #787878;
    border-color: #c2c2c2;
    border-style: solid;
    border-width: 1px;
}

h2{
    padding-top: 50px;
    text-align: center;
    font-weight: unset;
}

.title{
    display: flex;
    flex-direction: row;

}

.mobileFilter {
    width: 100%;
    display: flex;
    justify-content: center;
}


.pcFilter{
    display: none;
}

h1{
    margin: 0;
    padding-bottom: 10px;
}

.select {
    width: 143px;
    margin-left: auto;
    margin-right: 20px;
}
.pcPage{
    display: block;
}


@media only screen and (min-width: 768px) {
    .page {
        margin-top:  0;
    }

    .mobileFilter {
        display: none;
    }

    .pcPage{
        display: grid;
        grid-template-columns: 30% 70%;
    }

    .pcFilter{
        display: block;
    }
    .mobButton{
        display: none;
    }

    .select {
        width: 180px;
    }
    h2{
        padding-top: 150px;
    }
    h1{

        padding-bottom: 40px;
    }
}

@media only screen and (min-width: 992px) {
    .pcPage{
        grid-template-columns: 20% 80%;
    }
}