.header {
    grid-area: h;
    height: 105px;
    box-shadow: 0 6px 4px -4px rgba(0, 0, 0, .2);
    position: fixed;
    z-index: 10;
    width: 100%;
    display: block;
    background-color: white;
}

.iconButton{
    cursor: pointer;
}

.number{
    font-size: 17px;
    padding-left: 14px;
}

.top {
    padding-left: 2px;
    padding-top: 5px;
    height: 30px;
    background-color: black;
    color: white;
    font-size: larger;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.burger {
    position: fixed;
    display: inline-block;
    width: 40px;
    height: 40px;
    top: 53px;
    background-color: white;
    margin-bottom: 20px;
}

.burgerimg{
    width: 40px;
}

.bottom {
    padding: 10px 20px;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: white;
}

.topitem {
    padding-top: 2px;
    padding-right: 10px;
}
.search{
    text-decoration: none;
    color: white;
}
.search:hover{
    color: lightgrey;
}

.applogo {
    height: 30px;
    float: left;
}
.cartlogo {
    height: 30px;
    float: left;
}
.cart {
    float: right;
    padding: 10px 0 0 15px;
}

.navBlock{
    display: none;
}


.nav {
    color: #00d5be;
}

.logo {
    padding-top: 5px;
    height: 40px;
    padding-left: 55px;
    color: black;
    font-size: 35px;
    float: left;
    font-weight: bolder;
}

.links{
    display: none;
}

@media only screen and (min-width: 768px) {
    .navBlock{
        display: block;
        position: sticky;
    }
    .top{
        padding: 0 20px;
        height: 40px;
        display: grid;
        grid-template-columns: 30% 40% 28% 3%;
    }
    .cart{
        height: 3vh;
    }
    .logo{
        padding-top: 5px;
        padding-left: 100px;
        height: 11vh;
    }
    .applogo{
        height: 40px;
    }

    .topitem{
        padding-top: 5px;
    }
    .number{
        font-size: 22px;
    }
    .header{
        height: 195px;
        position: sticky;
    }
    .burger {
        display: none;
    }
    .bottom{
        height: 80px;
    }
    .cartlogo {
        margin-right: 50px;
        padding-top: 10px;
        height: 40px;
    }
    .links{
       display: flex;
        padding-top: 5px;
        flex-direction: row;
        margin-left: -200px;
    }
    .contacts{
        text-decoration: none;
        color: white;
        font-weight: bolder;
        padding-top: 5px;
        padding-right: 20px;
        font-size: smaller;
    }
    .contacts:hover{
        color: lightgrey;
    }


}