.page {
    margin-top: 120px;
    width: 100%;
}

.item {
    padding: 5px;
    display: flex;
    flex-direction: column;
}

.img {
    height: 320px;
}

.imgBlock {
    margin: 0 auto;
    padding: 10px;
}

.backContainer{
    margin-top: 5px;
    margin-left: 15px;
}

.back {
    text-decoration: none;
    color: black;
    font-size: larger;
}

.shortDescription {
    padding: 15px;
    margin: auto 0;
}

.desc {
    padding: 25px;
    font-size: large;
    text-align: left;
    color: #6f6f6f;
}

.buttoncont {
    padding: 10px;
    width: 280px;
    margin: 0 auto;
}

.but {
    background-color: black;
    border-radius: 10px;
    height: 50px;
    width: 280px;
    border: none;
    font-size: large;
    cursor: pointer;
    color: white;
    margin: 15px 0;
}

.title {
    padding: 20px 0;
    font-weight: bold;
    font-size: 35px;
    text-align: center;
}

.text span {
    font-weight: bolder;

}

.aval {
    font-size: 27px;
    padding: 15px;
    width: max-content;
    margin: 0 auto;
    color: black;
    font-weight: bolder;
}

.text {
    line-height: 35px;
    font-size: larger;
    padding-top: 7px;
    padding-left: 15px;
    color: #646464;
}

h1 {
    padding-top: 30px;
    text-align: center;
}

.textRed {
    color: red;
    line-height: 35px;
    font-size: 20px;
    text-align: center;
}


@media only screen and (min-width: 768px) {
    .page {
        margin: 0;
    }

    .backContainer{
        margin-left: 25px;
    }

    .but:hover {
        background-color: #272727;
    }

    .item {
        padding-top: 20px;
        width: max-content;
        margin-left: auto;
        margin-right: auto;
        height: max-content;
        flex-direction: row;
    }

    .img {
        height: 400px;
    }

    .imgBlock {
        margin: 0;
        padding: 0 30px;
    }

    .desc {
        padding: 0 100px 20px 100px;
    }
}